import React, { ChangeEvent, useContext, useEffect, useState } from 'react';
import Logo from '../../../assets/images/logo.png';
import { Link, useHistory } from 'react-router-dom';
import { ErrorMessage, Form, Formik, FormikHelpers } from 'formik';
import { SignUpInstructorValidator } from '../../../validators';
import { FormikConfig } from 'formik/dist/types';
import { API } from '../../../utility/api';
import { Alert } from 'react-bootstrap';
import { AlertProps } from 'react-bootstrap/Alert';
import { AuthContext } from '../../context';
import { FormikValidator } from '../../../utility';

interface AlertInterface {
  isAlert: boolean;
  message?: string;
  options?: AlertProps;
}

const SignUpAsInstructor = (): JSX.Element => {
  const [status, setStatus] = useState<AlertInterface>({
    isAlert: false,
  });
  const authContext = useContext(AuthContext);
  const history = useHistory();

  useEffect(() => {
    if (authContext.state.isLoggedIn) {
      history.push('/dashboard');
    }
  }, [authContext, history]);

  const handleUpload = (
    event: ChangeEvent<HTMLInputElement>,
    setFieldValue: (field: string, value: any, shouldValidate?: boolean) => void,
  ) => {
    const target = event.target as HTMLInputElement;
    const files = target.files;
    API.Media.uploadFile(files[0])
      .then((response) => {
        console.log(response.data);
        setFieldValue(target.name, response.data.path, true);
      })
      .catch((error) => {
        console.log(error);
      });
  };
  const formik: FormikConfig<SignUpInstructorValidator> = {
    initialValues: {
      firstName: '',
      lastName: '',
      email: '',
      phoneNumber: '',
      address1: '',
      password: '',
      zip: '',
      city: '',
      resume: '',
      state: '',
      bio: '',
      confirm_password: '',
    },
    onSubmit(
      values: SignUpInstructorValidator,
      formikHelpers: FormikHelpers<any>,
    ): void | Promise<any> {
      API.Auth.signUpInstructor(values)
        .then((response) => {
          console.log(response);
          setStatus({
            isAlert: true,
            message: 'Instructor Signup Successfully',
            options: {
              variant: 'success',
            },
          });
        })
        .catch((error) => {
          console.error(error.response);
          const errors = FormikValidator.serverErrors(error.response);
          if (errors) {
            formikHelpers.setErrors(errors);
          } else {
            setStatus({
              isAlert: true,
              message: 'Invalid Form Data',
              options: {
                variant: 'danger',
              },
            });
          }
        });
      return undefined;
    },
    validate: FormikValidator.validator(SignUpInstructorValidator),
  };

  return (
    <div className="instructor-form">
      {status.isAlert && <Alert {...(status.options || {})}> {status.message}</Alert>}
      <div className="form-showcase">
        <img alt="logo" src={Logo} />
        <h1>SignUp As Instructor</h1>
        <p>Create an account to see the workshops you have been selected for</p>
      </div>
      <Formik {...formik}>
        {(formikProps) => (
          <Form>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="firstName">First Name</label>
                <input
                  type="text"
                  className="input"
                  name="firstName"
                  onChange={formikProps.handleChange}
                  placeholder="Enter First Name"
                />
                <span className="error">
                  <ErrorMessage name="firstName" />
                </span>
              </div>
              <div className="input-field">
                <label htmlFor="lastName">Last Name</label>
                <input
                  type="text"
                  className="input"
                  name="lastName"
                  onChange={formikProps.handleChange}
                  placeholder="Enter Last Name"
                />
                <span className="error">
                  <ErrorMessage name="lastName" />
                </span>
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="email">Email Address</label>
                <input
                  type="email"
                  className="input"
                  name="email"
                  onChange={formikProps.handleChange}
                  placeholder="Email Address"
                />
                <ErrorMessage
                  name="email"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="number">Phone Number</label>
                <input
                  type="text"
                  className="input"
                  name="phoneNumber"
                  onChange={formikProps.handleChange}
                  placeholder="Phone Number"
                />
                <span className="error">
                  <ErrorMessage name="phoneNumber" />
                </span>
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="address">Address</label>
                <input
                  type="text"
                  className="input"
                  name="address1"
                  placeholder="Type Here"
                  onChange={formikProps.handleChange}
                />
                <span className="error">
                  <ErrorMessage name="address1" />
                </span>
              </div>
              <div className="input-fields">
                <div className="input-field">
                  <label htmlFor="city">City</label>
                  <select name="city" className="input" onChange={formikProps.handleChange}>
                    <option value="" disabled={true} selected={true}>
                      Select
                    </option>
                    <option value="value-2">value-2</option>
                    <option value="value-3">value-3</option>
                    <option value="value-4">value-4</option>
                  </select>
                  <span className="error">
                    <ErrorMessage name="city" />
                  </span>
                </div>
                <div className="input-field">
                  <label htmlFor="state">State</label>
                  <select name="state" className="input" onChange={formikProps.handleChange}>
                    <option value="" disabled={true} selected={true}>
                      Select
                    </option>
                    <option value="value-2">value-2</option>
                    <option value="value-3">value-3</option>
                    <option value="value-4">value-4</option>
                  </select>
                  <span className="error">
                    <ErrorMessage name="state" />
                  </span>
                </div>
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="type">Zip Code</label>
                <input
                  type="text"
                  className="input"
                  name="zip"
                  placeholder="Type Here"
                  onChange={formikProps.handleChange}
                />
                <span className="error">
                  <ErrorMessage name="zip" />
                </span>
              </div>
              <div className="input-field">
                <label htmlFor="resume">Upload Resume</label>
                <input
                  type="file"
                  id="resume"
                  name="resume"
                  className="input"
                  onChange={(event) => handleUpload(event, formikProps.setFieldValue)}
                />
                <ErrorMessage
                  name="resume"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="form-desc">
              <label htmlFor="description">
                Brief Bio <span>(Please provide a short paragraph about yourself)</span>
              </label>
              <textarea
                name="bio"
                className="input"
                cols={0}
                rows={3}
                placeholder="Type here"
                onChange={formikProps.handleChange}
              />

              <span className="error">
                <ErrorMessage name="bio" />
              </span>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="email">New Password</label>
                <input
                  type="password"
                  className="input"
                  name="password"
                  onChange={formikProps.handleChange}
                  placeholder="Password"
                />
                <span className="error">
                  <ErrorMessage name="password" />
                </span>
              </div>
              <div className="input-field">
                <label htmlFor="number">Confirm Password</label>
                <input
                  type="password"
                  className="input"
                  name="confirm_password"
                  onChange={formikProps.handleChange}
                  placeholder="Password"
                />
                <span className="error">
                  <ErrorMessage name="confirm_password" />
                </span>
              </div>
            </div>
            <div className="input-field">
              <button type="submit">Sign Up</button>
            </div>
            <p className="login">
              Already have an account? <Link to="/login"> Log In </Link> Now
            </p>
          </Form>
        )}
      </Formik>
    </div>
  );
};
export default SignUpAsInstructor;
