import React, { useContext, useState } from 'react';
import Graph from '../../components/Dashboard/components/Graph';
import picOne from '../../../assets/icons/Combined Shape 7.svg';
import picTwo from '../../../assets/icons/Combined Shape 5.svg';
import picThree from '../../../assets/icons/Fill 23.svg';
import DatePicker from 'react-date-picker';
import { StatusCard } from '../../components';
import { AuthContext } from '../../context';
import { CurrentWorkshops, RecentlyCreatedWorkshops } from './components';
import { UserRoleEnum } from '../../../interfaces';

export default function DashboardView(): JSX.Element {
  const [value, onChange] = useState<Date>(new Date());
  const authContext = useContext(AuthContext);

  return (
    <div>
      <div className="content">
        <div className="row">
          <div className="col-sm-12 col-md-6 col-lg-4">
            <StatusCard title="Past WorkShops" counter={400} image={picOne} variant="danger" />
          </div>
          <div className="col-sm-12 col-md-6 col-lg-4">
            <StatusCard title="Active WorkShops" counter={110} image={picTwo} variant="success" />
          </div>
          <div className="col-sm-12 col-md-6 col-lg-4">
            <StatusCard title="Script WorkShops" counter={35} image={picThree} variant="warning" />
          </div>
        </div>
      </div>

      {authContext.state.user.role === UserRoleEnum.admin && (
        <>
          <div className="past-workshop">
            <div className="past-workshop-heading">Past workshops</div>
            <div className="past-workshop-dropdown">
              <div>
                <DatePicker
                  className="custom-date-picker"
                  // @ts-ignore
                  onChange={onChange}
                  value={value}
                  calendarIcon={<span className="material-icons">expand_more</span>}
                  clearIcon={null}
                />
              </div>
              <div className="past-workshop-dropdown2">
                <div className="form-group m-0 h-100">
                  <select className="form-control h-100" id="exampleFormControlSelect1">
                    <option value="select the workshop" disabled selected>
                      Select the workshop
                    </option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
          <div className="graph">
            <Graph />
          </div>
        </>
      )}

      {authContext.state.user.role === UserRoleEnum.admin && <RecentlyCreatedWorkshops />}
      {[UserRoleEnum.instructor, UserRoleEnum.educator].includes(authContext.state.user.role) && (
        <CurrentWorkshops />
      )}
    </div>
  );
}
