import { Expose } from 'class-transformer';
import { IsEnum, IsNotEmpty, IsString, IsUrl } from 'class-validator';
import { Attachment } from '../interfaces/attachment.interface';
import { UserRoleEnum } from '../interfaces';

@Expose()
export class AttachmentValidator implements Attachment {
  @IsString()
  @IsNotEmpty()
  description: string;

  @IsUrl()
  @IsNotEmpty()
  link: string;

  @IsString()
  @IsNotEmpty()
  createdBy: string;

  @IsEnum(UserRoleEnum)
  @IsNotEmpty()
  createdByRole: UserRoleEnum;
}
