import React, { useContext, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import Logo from '../../../assets/images/logo.png';
import { API } from '../../../utility/api';
import { AuthContext } from '../../context';
import { FormikConfig } from 'formik/dist/types';
import { LoginFormValidator } from '../../../validators';
import { ErrorMessage, Form, Formik, FormikHelpers } from 'formik';
import { Dropdown } from 'react-bootstrap';
import { toast } from 'react-toastify';
import { UserRoleEnum } from '../../../interfaces';
import { FormikValidator } from '../../../utility';

const Login = (): JSX.Element => {
  const authContext = useContext(AuthContext);
  const history = useHistory();

  useEffect(() => {
    if (authContext.state.isLoggedIn) {
      if (authContext.state.user.role === UserRoleEnum.educator && !authContext.state.user.school) {
        history.push('/signup-educator');
      } else {
        history.push('/dashboard');
      }
    }
  }, [authContext]);

  const formik: FormikConfig<LoginFormValidator> = {
    initialValues: {
      identifier: '',
      password: '',
    },
    onSubmit(values: LoginFormValidator, formikHelpers: FormikHelpers<any>): void | Promise<any> {
      API.Auth.login(values)
        .then((response) => {
          authContext.actions.updateState(response.data);
          history.push('/dashboard');
          toast('User Logged in Successfully');
          console.log(response);
        })
        .catch((error) => {
          // toast(error?.response?.message);
          const errors = FormikValidator.serverErrors(error.response);
          if (errors) {
            formikHelpers.setErrors(errors);
          }
          console.error(error.response);
        });
    },

    validate: FormikValidator.validator(LoginFormValidator),
  };
  return (
    <div className="instructor-form login-form">
      <div className="form-showcase">
        <img src={Logo} alt="" />
        <h1>Login</h1>
        <p>Create an account to see the workshops you have been selected for</p>
      </div>
      <Formik {...formik}>
        {(formikProps) => (
          <Form>
            <div className="input-field">
              <label htmlFor="username">User Name</label>
              <input
                type="text"
                className="input"
                name="identifier"
                onChange={formikProps.handleChange}
                placeholder="Enter user Name"
              />
              <ErrorMessage
                name="identifier"
                render={(error) => <span className="error">{error}</span>}
              />
            </div>
            <div className="input-field">
              <label htmlFor="password">Password</label>
              <input
                type="password"
                className="input"
                name="password"
                onChange={formikProps.handleChange}
                placeholder="Enter Password"
              />
              <ErrorMessage
                name="password"
                render={(error) => <span className="error">{error}</span>}
              />
            </div>
            <div className="input-field">
              <button type="submit">Login</button>
            </div>
            <div className="login">
              <span>Don't have an account?</span>
              <Dropdown>
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                  Sign Up
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Link to="/signup-instructor" className="dropdown-item">
                    SignUp as Instructor
                  </Link>
                  <Link to="/signup-educator" className="dropdown-item">
                    SignUp as Educator
                  </Link>
                </Dropdown.Menu>
              </Dropdown>
              <span>Now</span>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default Login;
