import { createAsyncThunk } from '@reduxjs/toolkit';
import { API } from '../../../utility/api';
import { WorkshopInterface } from '../../../interfaces/workshop.interface';

export const workshopDetailThunk = createAsyncThunk<WorkshopInterface, string>(
  `workshop-detail-thunk`,
  async (id, thunkApi) => {
    try {
      const response = await API.Workshop.getById(id);
      return response.data;
    } catch (error) {
      console.log(error.response);
      return thunkApi.rejectWithValue(error.response);
    }
  },
);
