import React from 'react';
import { Route, Switch } from 'react-router-dom';
import MainLayout from './layouts';
import Login from './views/Login';
import SignUpAsInstructor from './views/SignUpAsInstructor';
import SignUpAsEducator from './views/SignUpAsEducator';
import ProtectedRoute from './components/ProtectedRoute';

export function Router(): JSX.Element {
  return (
    <Switch>
      <Route component={Login} path="/login" />
      <Route component={SignUpAsInstructor} path="/signup-instructor" />
      <Route component={SignUpAsEducator} path="/signup-educator" />
      <ProtectedRoute
        component={MainLayout}
        path="/"
        data={{
          all: true,
        }}
      />
      <Route component={MainLayout} path="/" />
    </Switch>
  );
}
