import React, { CSSProperties } from 'react';
import clsx from 'clsx';
import classes from './style.module.scss';

interface Props {
  width?: number | string;
  height?: number | string;
  circleSize?: number;
  variant: 'text' | 'circle' | 'rect';
}

export function Skeleton(props: Props): JSX.Element {
  const style: CSSProperties = {};
  if (props.width) {
    style.width = props.width;
  }
  if (props.height) {
    style.height = props.height;
  }
  if (props.circleSize) {
    style.height = props.circleSize;
    style.width = props.circleSize;
  }
  return (
    <span
      className={clsx(classes.root, classes.pulse, {
        [classes.circle]: props.variant === 'circle',
        [classes.text]: props.variant === 'text',
      })}
      style={style}
    />
  );
}
