import { IsDate, IsNotEmpty, IsString } from 'class-validator';
import { Transform } from 'class-transformer';

export class WorkshopAddDaysValidator {
  id?: string;

  @IsString()
  @IsNotEmpty()
  title: string;

  @IsDate()
  @IsNotEmpty()
  @Transform((params) => {
    if (params.value) {
      return new Date(params.value);
    }
  })
  date: string;

  @IsString()
  @IsNotEmpty()
  description: string;
}
