import React from 'react';
import { ErrorMessage, Form, Formik, FormikHelpers } from 'formik';
import { FormikConfig } from 'formik/dist/types';
import { SignUpEducatorPersonValidator } from '../../../../../validators';
import { API } from '../../../../../utility/api';
import { UserInterface } from '../../../../../interfaces';
import { FormikValidator } from '../../../../../utility';

type SignUpTabType = 'Personal' | 'School';

interface Props {
  onChangeTab: (tab: SignUpTabType) => void;
  updateUser: (userData: UserInterface) => void;
}

const PersonalDetails = (props: Props): JSX.Element => {
  const formik: FormikConfig<SignUpEducatorPersonValidator> = {
    initialValues: {
      firstName: '',
      lastName: '',
      email: '',
      summerEmail: '',
      address1: '',
      address2: '',
      city: '',
      state: '',
      zip: '',
      phoneNumber: '',
      password: '',
      confirm_password: '',
    },
    onSubmit(
      values: SignUpEducatorPersonValidator,
      formikHelpers: FormikHelpers<any>,
    ): void | Promise<any> {
      API.Auth.signUpAsEducatorPerson(values)
        .then((response) => {
          console.log(response);
          props.onChangeTab('School');
          props.updateUser(response.data);
          localStorage.setItem('access-token', response.data.authToken);
          formikHelpers.setSubmitting(false);
        })
        .catch((error) => {
          const errors = FormikValidator.serverErrors(error.response);
          if (errors) {
            formikHelpers.setErrors(errors);
          }
          formikHelpers.setSubmitting(false);
        });
    },
    validate: FormikValidator.validator(SignUpEducatorPersonValidator),
  };
  return (
    <div className="personal-details">
      <Formik {...formik}>
        {(formikProps) => (
          <Form>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="firstName">First Name</label>
                <input
                  type="text"
                  className="input"
                  name="firstName"
                  placeholder="Enter First Name"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="firstName"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="lastName">Last Name</label>
                <input
                  type="text"
                  className="input"
                  name="lastName"
                  placeholder="Enter Last Name"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="lastName"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="email">Email Address</label>
                <input
                  type="email"
                  className="input"
                  name="email"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="email"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="email">Summer Email Address</label>
                <input
                  type="email"
                  className="input"
                  name="summerEmail"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="summerEmail"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <h1 className="home-address">Address for sending Materials</h1>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="address">Address 1</label>
                <input
                  type="text"
                  className="input"
                  name="address1"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="address1"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="address">Address 2</label>
                <input
                  type="text"
                  className="input"
                  name="address2"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="address2"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="city">City</label>
                <select name="city" className="input" onChange={formikProps.handleChange}>
                  <option disabled={true} selected={true}>
                    Select
                  </option>
                  <option value="value-2">value-2</option>
                  <option value="value-3">value-3</option>
                  <option value="value-4">value-4</option>
                </select>
                <ErrorMessage
                  name="city"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="state">State</label>
                <select name="state" className="input" onChange={formikProps.handleChange}>
                  <option value="" disabled={true} selected={true}>
                    Select
                  </option>
                  <option value="value-2">value-2</option>
                  <option value="value-3">value-3</option>
                  <option value="value-4">value-4</option>
                </select>
                <ErrorMessage
                  name="state"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="zip">Zip Code</label>
                <input
                  type="text"
                  name="zip"
                  className="input"
                  placeholder="Zip Code"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="zip"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="number">Phone Number</label>
                <input
                  type="text"
                  className="input"
                  name="phoneNumber"
                  placeholder="Phone Number"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="phoneNumber"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>

            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="email">New Password</label>
                <input
                  type="password"
                  className="input"
                  name="password"
                  onChange={formikProps.handleChange}
                  placeholder="Password"
                />
                <span className="error">
                  <ErrorMessage name="password" />
                </span>
              </div>
              <div className="input-field">
                <label htmlFor="number">Confirm Password</label>
                <input
                  type="password"
                  className="input"
                  name="confirm_password"
                  onChange={formikProps.handleChange}
                  placeholder="Password"
                />
                <span className="error">
                  <ErrorMessage name="confirm_password" />
                </span>
              </div>
            </div>
            <div className="input-field">
              <button>Save and Continue</button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default PersonalDetails;
