import React, { useContext } from 'react';
import logo from '../../../assets/images/Bitmap.png';
import dashboardIcon from '../../../assets/icons/Combined Shape.svg';
import workshopsIcon from '../../../assets/icons/Combined Shape-11.svg';
import scriptIcon from '../../../assets/icons/Fill 1.svg';
import instructorsIcon from '../../../assets/icons/Combined Shape 3.svg';
import educatorsIcon from '../../../assets/icons/Combined Shape-2.svg';
import { NavLink } from 'react-router-dom';
import { AuthContext } from '../../context';
import { UserRoleEnum } from '../../../interfaces';

const Navbar = (): JSX.Element => {
  const authContext = useContext(AuthContext);

  return (
    <nav className="sidenav">
      <div className="logo">
        <img src={logo} />
      </div>
      <ul className="nav-items">
        <li className="nav-item">
          <NavLink to="/dashboard">
            <img className="icon" src={dashboardIcon} alt="" />
            <span className="title">Dashboard</span>
          </NavLink>
        </li>
        {[UserRoleEnum.admin].includes(authContext.state.user.role) && (
          <>
            <li className="nav-item">
              <NavLink to="/workshops">
                <img className="icon" src={workshopsIcon} alt="" />{' '}
                <span className="title">WorkShops</span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to="/script-workshop-view">
                <img className="icon" src={scriptIcon} alt="" />{' '}
                <span className="title">SCRIPT WorkShops</span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to="/instructors">
                <img className="icon" src={instructorsIcon} alt="" />{' '}
                <span className="title">Instructors</span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to="/educators">
                <img className="icon" src={educatorsIcon} alt="" />{' '}
                <span className="title">Educators</span>
              </NavLink>
            </li>
          </>
        )}

        {[UserRoleEnum.educator, UserRoleEnum.instructor].includes(authContext.state.user.role) && (
          <>
            <li className="nav-item">
              <NavLink to="/current-workshop">
                <img className="icon" src={educatorsIcon} alt="" />{' '}
                <span className="title">Current Workshop</span>
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to="/past-workshop">
                <img className="icon" src={educatorsIcon} alt="" />{' '}
                <span className="title">Past Workshop</span>
              </NavLink>
            </li>
          </>
        )}

        <li className="nav-item">
          <NavLink to="/attendance">
            <img className="icon" src={educatorsIcon} alt="" />
            <span className="title">Attendance</span>
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};
export default Navbar;
