import React from 'react';
import clsx from 'clsx';

interface Props {
  image: string;
  counter: number;
  title: string;
  variant: 'danger' | 'success' | 'warning';
}

export function StatusCard(props: Props): JSX.Element {
  return (
    <div className="card status-card">
      <div className="card-body w-100 p-0">
        <div className="status-text">
          <div
            className={clsx('counter', {
              'text-success': props.variant === 'success',
              'text-danger': props.variant === 'danger',
              'text-warning': props.variant === 'warning',
            })}>
            {props.counter}
          </div>
          <div className="title">{props.title}</div>
        </div>
        <div
          className={clsx('status-icon', {
            'bg-success-light': props.variant === 'success',
            'bg-danger-light': props.variant === 'danger',
            'bg-warning-light': props.variant === 'warning',
          })}>
          <img className="icon" src={props.image} alt={props.title} />
        </div>
      </div>
    </div>
  );
}
