import React, { useEffect } from 'react';
import Instructors from '../../components/Instructors';
import { useAppDispatch, useAppSelector } from '../../../redux';
import { instructorListThunk } from './thunk';

export default function InstructorsView(): JSX.Element {
  const instructors = useAppSelector((state) => state.instructors);
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(instructorListThunk());
  }, [dispatch]);

  return (
    <div className="instructors-section same-btns">
      <div className="user-section">
        <div className="user-work">
          <h1>All Instructors</h1>
          <div className="user-work-btns">
            <button className="csv">
              <span className="material-icons">description</span>CSV
            </button>
            <div className="search-insturtor">
              <input type="text" placeholder="Search here" id="search" />
              <button>
                <span className="material-icons">search</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="content all-instructors">
        {instructors.data.map((item, index) => (
          <div key={index}>
            <Instructors data={item} loading={false} />
          </div>
        ))}
      </div>
    </div>
  );
}
