import React from 'react';
import { Link } from 'react-router-dom';
import phoneIcon from '../../../assets/icons/phone.svg';
import { Skeleton } from '../Skeleton';
import { UserInterface } from '../../../interfaces';
import instructorOne from '../../../assets/images/Mask.png';

interface Props {
  data?: UserInterface;
  loading?: boolean;
}

const Instructors = (props: Props): JSX.Element => {
  const { data, loading } = props;
  return (
    <div className="instructor">
      <div className="flex">
        <div className="instructor-img">
          {!loading && (
            <>
              <img src={data.profile || instructorOne} />
            </>
          )}
          {loading && <Skeleton variant="circle" circleSize={70} />}
        </div>
        <div className="flex">
          <div className="instructor-desc">
            {!loading && (
              <>
                <Link to="/instructorprofile">
                  {data.firstName} {data.lastName}
                </Link>
              </>
            )}
            {loading && <Skeleton variant="text" width="150px" height="24px" />}
            {!loading && (
              <>
                <p>
                  <span className="material-icons">mail_outline</span>
                  {data.email}
                </p>
              </>
            )}
            {loading && <Skeleton variant="text" width="120px" height="20px" />}

            {!loading && (
              <>
                <p>
                  <img src={phoneIcon} />
                  {data.phoneNumber}
                </p>
              </>
            )}
            {loading && <Skeleton variant="text" width="120px" height="20px" />}
          </div>
        </div>
      </div>
      <div className="instructor-del">
        {!loading && (
          <>
            <span className="material-icons">delete_outline</span>
          </>
        )}
        {loading && <Skeleton variant="text" width="20px" height="30px" />}
      </div>
    </div>
  );
};
export default Instructors;
