import React from 'react';
import { FormikValidator } from '../../../utility';
import { FormikConfig } from 'formik/dist/types';
import { UpdateWorkshopFromValidator } from '../../../validators';
import { Formik, FormikHelpers } from 'formik';
import { API } from '../../../utility/api';
import { toast } from 'react-toastify';
import { RouteChildrenProps } from 'react-router';
import { UpdateWorkshopForm } from './components';
import { useAuthContext } from '../../context';

export default function UpdateWorkshopView(props: RouteChildrenProps<{ id: string }>): JSX.Element {
  const authContext = useAuthContext();
  const formik: FormikConfig<UpdateWorkshopFromValidator> = {
    initialValues: {
      title: '',
      type: '',
      banner: {
        link: '',
        createdBy: '',
        description: '',
        createdByRole: authContext.state.user.role,
      },
      instructorEmail: '',
      link: '',
      location: '',
      description: '',
      data: [{ date: '', title: '', description: '' }],
    },
    validateOnBlur: true,
    validateOnChange: true,
    onSubmit(values, formikHelpers: FormikHelpers<any>): void | Promise<any> {
      API.Workshop.patch(props.match.params.id, values)
        .then((response) => {
          console.log(response);
          if (values.data.length) {
            API.Workshop.addDays(response.data.id, values.data)
              .then((res) => {
                document.body.scrollTop = 0; // For Safari
                document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
                toast('Workshop Created Successfully');
                formikHelpers.resetForm();
              })
              .catch((errors) => {
                toast(errors.response.message);
                console.error(errors.response);
              });
          } else {
            toast('Workshop Created Successfully');
            formikHelpers.resetForm();
          }
        })
        .catch((error) => {
          toast(error.response.message);
          console.error(error.response);
        });
    },
    validate: FormikValidator.validator(UpdateWorkshopFromValidator),
  };
  return (
    <div className="content create-workshops">
      <h1 className="heading">Update Workshop</h1>

      <Formik {...formik}>
        {(formikProps) => (
          <UpdateWorkshopForm formikProps={formikProps} id={props.match.params.id} />
        )}
      </Formik>
    </div>
  );
}
