import React, { useState } from 'react';
import plusIcon from '../../../../../assets/icons/plus.svg';
import minusIcon from '../../../../../assets/icons/minus.svg';
import { Accordion, Button, Card } from 'react-bootstrap';
import { useAppSelector } from '../../../../../redux';

const DetailsDay = (): JSX.Element => {
  const workshop = useAppSelector((state) => state.workshopDetail);
  const [activeKey, setActiveKey] = useState('');
  return (
    <div>
      <div className="day-by-day">
        <Accordion
          onSelect={(key) => {
            setActiveKey(key);
          }}>
          {workshop.data.days.map((day, index) => (
            <Card key={index}>
              <Card.Header>
                <Accordion.Toggle as={Button} variant="link" eventKey={day.id} className="day">
                  <div className="day-head">
                    <h1>Day {index + 1} </h1>
                    <p>{day.title}</p>
                  </div>
                  <div className="day-img">
                    {activeKey === day.id && <img alt="" src={minusIcon} />}
                    {activeKey !== day.id && <img alt="" src={plusIcon} />}
                  </div>
                </Accordion.Toggle>
              </Card.Header>
              <Accordion.Collapse eventKey={day.id}>
                <Card.Body>{day.description}</Card.Body>
              </Accordion.Collapse>
            </Card>
          ))}
        </Accordion>
      </div>
    </div>
  );
};

export default DetailsDay;
