import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import moment from 'moment-timezone';
import { Skeleton } from '../Skeleton';
import { AuthContext } from '../../context';
import { UserRoleEnum } from '../../../interfaces';
import { WorkshopInterface } from '../../../interfaces/workshop.interface';
import { getIn } from 'formik';

interface Props {
  data?: WorkshopInterface;
  loading?: boolean;
}

export function WorkshopCard(props: Props): JSX.Element {
  const authContext = useContext(AuthContext);
  const { data, loading } = props;
  const date = moment(getIn(data, 'days.0.date')).utc();
  const formattedTime = !!data
    ? `${date.tz('America/New_York').format('h a z')} / ${date
        .tz('America/Los_Angeles')
        .format('h a z')}`
        .replace('PDT', 'PST')
        .replace('EDT', 'EST')
    : '';
  const formattedDate = !!data ? `${date.format('dddd, MMM DD')}th` : '';

  return (
    <div className="card workshop-card">
      <div className="card-header">
        {!loading && (
          <>
            <img src={getIn(data, 'banner.link')} alt={data.title} className="image" />
            {data.type && <div className="info-tag">{data.type}</div>}
          </>
        )}

        {loading && <Skeleton variant="rect" width="100%" height="100%" />}
      </div>
      <div className="card-body">
        <div className="title">
          {!loading && <>{data.title}</>}
          {loading && <Skeleton variant="text" width="100%" height={25} />}
        </div>
        <div className="author">
          {!loading && <>{data.author && <>- By {data.author}</>}</>}
          {loading && <Skeleton variant="text" width="35%" height={25} />}
        </div>
        <div className="workshop-info-box">
          <div className="workshop-info">
            {!loading && (
              <>
                <span className="material-icons icon">event</span>
                <span className="text">{formattedDate}</span>
              </>
            )}
            {loading && <Skeleton variant="text" width="90%" height={20} />}
          </div>
          <div className="workshop-info">
            {!loading && (
              <>
                <span className="material-icons icon">watch_later</span>
                <span className="text">{formattedTime}</span>
              </>
            )}
            {loading && <Skeleton variant="text" width="90%" height={20} />}
          </div>

          <div className="workshop-info">
            {!loading && (
              <>
                <span className="material-icons icon">location_on</span>
                <span className="text">{data.location}</span>
              </>
            )}
            {loading && <Skeleton variant="text" width="50%" height={20} />}
          </div>
        </div>
        <div className="card-action">
          {!loading && (
            <>
              <Link
                to={`/workshop-detail/${props.data.id}`}
                className="btn btn-primary detail-button">
                See Details
              </Link>
            </>
          )}
          {loading && (
            <Skeleton
              variant="rect"
              width={authContext.state.user.role === UserRoleEnum.admin ? '70%' : '100%'}
              height="100%"
            />
          )}

          {!loading && (
            <>
              {authContext.state.user.role === UserRoleEnum.admin && (
                <Link
                  to={`/update-workshop/${props.data.id}`}
                  className="btn btn-light-success edit-button">
                  <span className="material-icons text-success">edit</span>
                  <span>Edit</span>
                </Link>
              )}
            </>
          )}
          {loading && (
            <>
              {authContext.state.user.role === UserRoleEnum.admin && (
                <Skeleton variant="rect" width="30%" height="100%" />
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
}
