import React, { useContext, useEffect, useState } from 'react';
import Logo from '../../../assets/images/logo.png';
import clsx from 'clsx';
import PersonalDetails from './components/PersonalDetails';
import SchoolDetails from './components/SchoolDetails';
import { AuthContext } from '../../context';
import { Link, useHistory } from 'react-router-dom';
import { UserInterface, UserRoleEnum } from '../../../interfaces';

type SignUpTabType = 'Personal' | 'School';

const SignUpAsEducator = (): JSX.Element => {
  const [signUpType, setSignUpType] = useState<SignUpTabType>('Personal');
  const [user, setUser] = useState<UserInterface>(null);
  const authContext = useContext(AuthContext);
  const history = useHistory();
  const updateUser = (userData: UserInterface) => {
    setUser(userData);
  };

  const onClickLogin = () => {
    authContext.actions.updateState(null);
  };

  useEffect(() => {
    if (authContext.state.isLoggedIn) {
      if (
        authContext.state.user.role === UserRoleEnum.educator &&
        !!authContext.state.user.school
      ) {
        history.push('/dashboard');
      } else {
        setSignUpType('School');
      }
    }
  }, [authContext, history]);

  const onChangeTab = (tab: SignUpTabType) => {
    setSignUpType(tab);
  };

  return (
    <div className="instructor-form">
      <div className="form-showcase">
        <img src={Logo} alt="" />
        <h1>SignUp As Educator</h1>
        <p>Create an account to see the workshops you are going to attend</p>
      </div>
      <div className="signup-type-btns">
        <div className={clsx('tab', { active: signUpType === 'Personal' })}>Personal Details</div>
        <div className={clsx('tab', { active: signUpType === 'School' })}>School Details</div>
      </div>
      {signUpType === 'Personal' && (
        <PersonalDetails updateUser={updateUser} onChangeTab={onChangeTab} />
      )}
      {signUpType === 'School' && <SchoolDetails user={user} onChangeTab={onChangeTab} />}
      <div className="form-bottom-text">
        <span>Already have an account?</span>
        <Link to="/login" onClick={onClickLogin}>
          Log In
        </Link>
        <span>Now</span>
      </div>
    </div>
  );
};

export default SignUpAsEducator;
