import React from 'react';
import { useAppSelector } from '../../../../../redux';
import moment from 'moment-timezone';

const DetailsMain = (): JSX.Element => {
  const workshop = useAppSelector((state) => state.workshopDetail);
  const date = moment(workshop.data?.days[0]?.date);
  const formattedTime = !!workshop.data
    ? `${date.tz('America/New_York').format('h a z')} / ${date
        .tz('America/Los_Angeles')
        .format('h a z')}`
        .replace('PDT', 'PST')
        .replace('EDT', 'EST')
    : '';
  const formattedDate = !!workshop.data ? `${date.format('dddd, MMM DD')}th` : '';
  return (
    <div className="details-desc">
      <h1>{workshop.data.title}</h1>
      <ul>
        <li>
          <span className="material-icons">event</span>
          {formattedDate}
        </li>
        <li>
          <span className="material-icons">watch_later</span>
          {formattedTime}
        </li>
        <li>
          <span className="material-icons">location_on</span>
          {workshop.data.location}
        </li>
      </ul>
      <img
        src={workshop.data.banner.link}
        alt={workshop.data.title}
        style={{
          objectFit: 'cover',
          maxHeight: 400,
          borderRadius: '20px',
        }}
      />
      <p>{workshop.data.description}</p>
    </div>
  );
};

export default DetailsMain;
