import {
  IsEmail,
  IsNotEmpty,
  IsNumber,
  IsOptional,
  IsPhoneNumber,
  IsString,
  IsUrl,
  ValidateNested,
} from 'class-validator';
import { Transform, Type } from 'class-transformer';

export class UpdateUserValidator {
  @IsString()
  @IsNotEmpty()
  firstName: string;

  @IsString()
  @IsNotEmpty()
  lastName: string;

  @IsEmail()
  @IsNotEmpty()
  email: string;

  @IsEmail()
  @IsOptional()
  summerEmail: string;

  @IsString()
  @IsNotEmpty()
  address1: string;

  @IsString()
  @IsOptional()
  address2: string;

  @IsString()
  @IsNotEmpty()
  city: string;

  @IsString()
  @IsNotEmpty()
  state: string;

  @IsString()
  @IsNotEmpty()
  zip: string;

  @IsString()
  @IsNotEmpty()
  bio: string;

  @IsUrl()
  @IsOptional()
  resume: string;

  @IsUrl()
  @IsOptional()
  profile: string;

  @IsPhoneNumber()
  @IsNotEmpty()
  @Transform(
    ({ value }) => {
      return value.startsWith('+') ? value : '+' + value;
    },
    { toClassOnly: true },
  )
  phoneNumber: string;
}

export class UpdateEducatorValidator {
  @IsString()
  @IsNotEmpty()
  firstName: string;

  @IsString()
  @IsNotEmpty()
  lastName: string;

  @IsEmail()
  @IsNotEmpty()
  email: string;

  @IsEmail()
  @IsNotEmpty()
  summerEmail: string;

  @IsString()
  @IsNotEmpty()
  address1: string;

  @IsString()
  @IsOptional()
  address2: string;

  @IsString()
  @IsNotEmpty()
  city: string;

  @IsString()
  @IsNotEmpty()
  state: string;

  @IsString()
  @IsNotEmpty()
  zip: string;

  @IsString()
  @IsNotEmpty()
  bio: string;

  @IsUrl()
  @IsOptional()
  resume: string;

  @IsUrl()
  @IsOptional()
  profile: string;

  @IsPhoneNumber()
  @IsNotEmpty()
  @Transform(
    ({ value }) => {
      return value.startsWith('+') ? value : '+' + value;
    },
    { toClassOnly: true },
  )
  phoneNumber: string;

  @ValidateNested()
  @IsNotEmpty()
  @Type(() => SchoolValidator)
  school: SchoolValidator;
}

class SchoolValidator {
  @IsString()
  @IsNotEmpty()
  district: string;

  @IsString()
  @IsNotEmpty()
  name: string;

  @IsString()
  @IsNotEmpty()
  setting: string;

  @IsString()
  @IsNotEmpty()
  type: string;

  @IsString()
  @IsNotEmpty()
  role: string;

  @IsNumber()
  @IsNotEmpty()
  students: number;

  @IsString()
  @IsNotEmpty()
  licenceAreas: string;

  @IsString()
  @IsNotEmpty()
  grads: string;

  @IsString()
  @IsNotEmpty()
  experience: string;

  @IsString()
  @IsNotEmpty()
  race: string;

  @IsString()
  @IsNotEmpty()
  condition: string;
}
