export enum GenderEnum {
  male = 'male',
  female = 'female',
}

export enum UserRoleEnum {
  admin = 'admin',
  educator = 'educator',
  instructor = 'instructor',
}

export interface SchoolDataInterface {
  district: string;
  name: string;
  setting: string;
  type: string;
  role: string;
  students: number;
  licenceAreas: string;
  grads: string;
  experience: string;
  race: string;
  condition: string;
}

export interface UserInterface {
  id: string;
  firstName: string;
  lastName: string;
  email: string;
  phoneNumber: string;
  location: string;
  gender: GenderEnum;
  address: string;
  city: string;
  state: string;
  profile: string;
  resume: string;
  bio: string;
  zip: string;
  address1: string;
  address2: string;
  summerEmail: string;
  emailVerified: boolean;
  phoneNumberVerified: boolean;
  role: UserRoleEnum;
  needPasswordChange: boolean;
  school?: SchoolDataInterface;
  authToken: string;
}
