import { createSlice } from '@reduxjs/toolkit';
import {
  myWorkshopsListThunk,
  workshopsListByEducatorThunk,
  workshopsListByInstructorThunk,
  workshopsListThunk,
} from './thunk';
import { WorkshopInterface } from '../../interfaces/workshop.interface';

interface RegisterState {
  data: WorkshopInterface[];
  myWorkshops: WorkshopInterface[];
  educatorWorkshops: WorkshopInterface[];
  instructorWorkshops: WorkshopInterface[];
  loading: boolean;
  error: any;
  statusCode: number;
}

const initialState: RegisterState = {
  data: [],
  myWorkshops: [],
  educatorWorkshops: [],
  instructorWorkshops: [],
  loading: false,
  error: null,
  statusCode: null,
};

export const workshopsListSlice = createSlice({
  name: 'workshops-list-slice',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    // Workshops List
    builder.addCase(workshopsListThunk.pending, (state, { payload }) => {
      // state.data = [];
      state.loading = true;
      state.error = null;
    });
    builder.addCase(workshopsListThunk.fulfilled, (state, { payload }) => {
      state.data = payload;
      state.loading = false;
      state.error = null;
    });
    builder.addCase(workshopsListThunk.rejected, (state, action) => {
      if (action.payload) {
        state.error = action.payload;
      } else {
        state.error = action.error;
      }
      state.loading = false;
    });

    // My Workshops List
    builder.addCase(myWorkshopsListThunk.pending, (state, { payload }) => {
      // state.data = [];
      state.loading = true;
      state.error = null;
    });
    builder.addCase(myWorkshopsListThunk.fulfilled, (state, { payload }) => {
      state.myWorkshops = payload;
      state.loading = false;
      state.error = null;
    });
    builder.addCase(myWorkshopsListThunk.rejected, (state, action) => {
      if (action.payload) {
        state.error = action.payload;
      } else {
        state.error = action.error;
      }
      state.loading = false;
    });

    // Workshops List By Educator
    builder.addCase(workshopsListByEducatorThunk.pending, (state, { payload }) => {
      // state.data = [];
      state.loading = true;
      state.error = null;
    });
    builder.addCase(workshopsListByEducatorThunk.fulfilled, (state, { payload }) => {
      state.educatorWorkshops = payload;
      state.loading = false;
      state.error = null;
    });
    builder.addCase(workshopsListByEducatorThunk.rejected, (state, action) => {
      if (action.payload) {
        state.error = action.payload;
      } else {
        state.error = action.error;
      }
      state.loading = false;
    });

    // Workshops List By Instructor
    builder.addCase(workshopsListByInstructorThunk.pending, (state, { payload }) => {
      // state.data = [];
      state.loading = true;
      state.error = null;
    });
    builder.addCase(workshopsListByInstructorThunk.fulfilled, (state, { payload }) => {
      state.instructorWorkshops = payload;
      state.loading = false;
      state.error = null;
    });
    builder.addCase(workshopsListByInstructorThunk.rejected, (state, action) => {
      if (action.payload) {
        state.error = action.payload;
      } else {
        state.error = action.error;
      }
      state.loading = false;
    });
  },
});
