import { IsEmail, IsNotEmpty, IsString, IsUrl, ValidateNested } from 'class-validator';
import { Expose, Type } from 'class-transformer';
import { WorkshopAddDaysValidator } from './WorkshopAddDays.validator';
import { AttachmentValidator } from '../Attachment.validator';

@Expose()
export class CreateWorkshopFromValidator {
  @IsString()
  @IsNotEmpty()
  title: string;

  @IsString()
  @IsNotEmpty()
  type: string;

  @IsEmail()
  @IsNotEmpty()
  instructorEmail: string;

  @IsUrl()
  @IsNotEmpty()
  link: string;

  @IsString({ message: 'address must be a string' })
  @IsNotEmpty({ message: 'address should not be empty' })
  location: string;

  @ValidateNested()
  @IsNotEmpty()
  @Type(() => AttachmentValidator)
  banner: AttachmentValidator;

  @ValidateNested()
  @IsNotEmpty()
  @Type(() => WorkshopAddDaysValidator)
  data: WorkshopAddDaysValidator[];

  @IsString()
  @IsNotEmpty()
  description: string;
}
