import React, { Component } from 'react';
import { loadScript } from '../../../../../utility';

class Graph extends Component {
  async componentDidMount() {
    await loadScript({
      id: 'google-chart-sdk',
      async: true,
      src: 'https://www.gstatic.com/charts/loader.js',
    });
    const columnchart = document.querySelector<HTMLDivElement>('#columnchart_material');
    if (columnchart) {
      const parent: HTMLDivElement = columnchart.parentElement as HTMLDivElement;
      const rect = parent.getBoundingClientRect();
      columnchart.style.width = `${rect.width}px`;
      columnchart.style.height = `${rect.height}px`;
    }
    google.charts.load('current', { packages: ['bar'] });
    google.charts.setOnLoadCallback(drawChart);

    function drawChart() {
      var data = google.visualization.arrayToDataTable([
        ['Year', 'Sales', 'Expenses', 'Profit'],
        ['2014', 800, 400, 200],
        ['2015', 1170, 460, 250],
        ['2016', 660, 1120, 300],
        ['2018', 1030, 540, 350],
        ['2019', 1030, 540, 350],
        ['2020', 1030, 540, 350],
        ['2021', 1030, 540, 350],
      ]);

      var options = {
        chart: {
          title: 'Company Performance',
          subtitle: 'Sales, Expenses, and Profit: 2014-2017',
        },
      };

      var chart = new (google.charts as any).Bar(document.getElementById('columnchart_material'));

      chart.draw(data, (google.charts as any).Bar.convertOptions(options));
    }
  }

  render(): JSX.Element {
    return (
      <div
        id="columnchart_material"
        style={{
          width: '800px',
          height: '500px',
        }}></div>
    );
  }
}

export default Graph;
