import React, { memo, useContext } from 'react';
import { RouteProps } from 'react-router';
import { Redirect, Route } from 'react-router-dom';
import { AuthContext, AuthContextInterface } from '../../context';
import { UserRoleEnum } from '../../../interfaces';

interface RouteData {
  role?: UserRoleEnum;
  roles?: UserRoleEnum[];
  all?: boolean;
}

// type Without<T, U> = { [P in Exclude<keyof T, keyof U>]?: never };
// type XOR<T, U> = T | U extends object ? (Without<T, U> & U) | (Without<U, T> & T) : T | U;

// type RouteData = AllAllow | RolesAllow | RoleAllow;

interface ProtectedRouteProps extends RouteProps {
  data: RouteData;
}

function ProtectedRoute(props: ProtectedRouteProps): JSX.Element {
  const context = useContext<AuthContextInterface>(AuthContext);
  const routeData: RouteData = props.data;
  if (context.state.isLoggedIn) {
    if (context.state.user.role === UserRoleEnum.educator && !context.state.user.school) {
      return <Route {...props} component={(): JSX.Element => <Redirect to="/signup-educator" />} />;
    } else if (
      routeData.all ||
      (!!routeData.role && routeData.role === context.state.user.role) ||
      (!!routeData.roles && routeData.roles.includes(context.state.user.role))
    ) {
      return <Route {...props} />;
    }
  }
  return <Route {...props} component={(): JSX.Element => <Redirect to="/login" />} />;
}

export default memo(ProtectedRoute);
