import React, { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../redux';
import picSix from '../../../../../assets/icons/Group 5.png';
import arrow from '../../../../../assets/icons/rightarrow.png';
import { WorkshopCard } from '../../../../components';
import { useAuthContext } from '../../../../context';
import {
  workshopsListByEducatorThunk,
  workshopsListByInstructorThunk,
} from '../../../../../redux/workshops';
import { UserRoleEnum } from '../../../../../interfaces';

export function CurrentWorkshops(): JSX.Element {
  const authContext = useAuthContext();
  const workshops = useAppSelector((state) => state.workshopsList);
  const dispatch = useAppDispatch();
  useEffect(() => {
    if (authContext.state.user.role === UserRoleEnum.educator) {
      dispatch(workshopsListByEducatorThunk(authContext.state.user.id));
    } else if (authContext.state.user.role === UserRoleEnum.instructor) {
      dispatch(workshopsListByInstructorThunk(authContext.state.user.id));
    }
  }, [authContext.state.user]);
  return (
    <>
      <div className="recently-created-workshop">
        <div className="recently-created-workshop-heading ">Current Workshops</div>
        <div className="combine-arrow-button">
          <div className="recently-created-workshop-button ">
            <button type="button" className="btn bg-white">
              See All
            </button>
          </div>
          <div className="recently-created-workshop-arrows">
            <div className="left-arrow">
              <div className="placement-arrow">
                <img src={picSix} />
              </div>
            </div>
            <div className="right-arrow">
              <div className="placement-arrow">
                <img src={arrow} />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="workspace-card-box">
        <div className="row">
          {!workshops.loading && (
            <>
              {authContext.state.user.role === UserRoleEnum.instructor &&
                workshops.instructorWorkshops.slice(0, 3).map((item, index) => (
                  <div className="col-4" key={index}>
                    <WorkshopCard data={item} loading={false} />
                  </div>
                ))}
              {authContext.state.user.role === UserRoleEnum.educator &&
                workshops.educatorWorkshops.slice(0, 3).map((item, index) => (
                  <div className="col-4" key={index}>
                    <WorkshopCard data={item} loading={false} />
                  </div>
                ))}
            </>
          )}

          {workshops.loading &&
            Array(3)
              .fill(1)
              .map((item, index) => (
                <div className="col-4" key={index}>
                  <WorkshopCard loading={true} />
                </div>
              ))}
        </div>
      </div>
    </>
  );
}
