import React, { ChangeEvent, useContext } from 'react';
import { DragAndDropImage } from '../../components';
import { FormikValidator, numberToWords } from '../../../utility';
import { FormikConfig } from 'formik/dist/types';
import { CreateWorkshopFromValidator } from '../../../validators';
import { ErrorMessage, Form, Formik, FormikHelpers, getIn } from 'formik';
import { API } from '../../../utility/api';
import { toast } from 'react-toastify';
import { AuthContext } from '../../context';
import { WorkshopAddDaysValidator } from '../../../validators/workshop/WorkshopAddDays.validator';
import moment from 'moment';

type SetFieldValue = (field: string, value: any, shouldValidate?: boolean) => void;

interface CustomFormikChange {
  event: ChangeEvent<HTMLInputElement>;
  value: any;
  onChange: SetFieldValue;
}

interface AddWorkshopDays {
  group: string;
  values: WorkshopAddDaysValidator[];
  onChange: SetFieldValue;
}

interface RemoveWorkshopDays extends AddWorkshopDays {
  index: number;
}

export default function CreateWorkshopView(): JSX.Element {
  const authContext = useContext(AuthContext);
  const onChangeDragZone = (field: string, value: any, setFieldValue: SetFieldValue) => {
    setFieldValue(`${field}.createdAt`, new Date(), true);
    setFieldValue(`${field}.link`, value, true);
    setFieldValue(`${field}.createdBy`, authContext.state.user.id, true);
    setFieldValue(`${field}.description`, 'Workshop', true);
  };

  const onChangeDate = (event: CustomFormikChange) => {
    event.event.persist();
    const target = event.event.target as HTMLInputElement;
    event.onChange(target.name, new Date(target.value));
  };
  const onChangeTime = (event: CustomFormikChange) => {
    event.event.persist();
    const target = event.event.target as HTMLInputElement;
    if (event.value) {
      const oldDate = new Date(event.value).toLocaleDateString();
      event.onChange(target.name, new Date(`${oldDate} ${target.value}`));
    } else {
      event.onChange(target.name, new Date(`${new Date().toLocaleDateString()} ${target.value}`));
    }
  };

  const onAddDay = (event: AddWorkshopDays) => {
    const values: WorkshopAddDaysValidator[] = event.values || [];
    values.push({
      title: '',
      date: '',
      description: '',
    });
    event.onChange(event.group, values);
  };
  const onRemoveDay = (event: RemoveWorkshopDays) => {
    const values: WorkshopAddDaysValidator[] = event.values || [];
    values.splice(event.index, 1);
    event.onChange(event.group, values);
  };

  const formik: FormikConfig<CreateWorkshopFromValidator> = {
    initialValues: {
      title: '',
      type: '',
      banner: {
        link: '',
        createdBy: '',
        description: '',
        createdByRole: authContext.state.user.role,
      },
      instructorEmail: '',
      link: '',
      location: '',
      description: '',
      data: [{ date: '', title: '', description: '' }],
    },
    validateOnBlur: true,
    validateOnChange: true,
    onSubmit(values, formikHelpers: FormikHelpers<any>): void | Promise<any> {
      API.Workshop.create(values)
        .then((response) => {
          console.log(response);
          if (values.data.length) {
            API.Workshop.addDays(response.data.id, values.data)
              .then((res) => {
                document.body.scrollTop = 0; // For Safari
                document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
                toast('Workshop Created Successfully');
                formikHelpers.resetForm();
              })
              .catch((errors) => {
                toast(errors.response.message);
                console.error(errors.response);
              });
          } else {
            toast('Workshop Created Successfully');
            formikHelpers.resetForm();
          }
        })
        .catch((error) => {
          toast(error.response.message);
          console.error(error.response);
        });
    },
    validate: FormikValidator.validator(CreateWorkshopFromValidator),
  };
  return (
    <div className="content create-workshops">
      <h1 className="heading">Create Workshop</h1>

      <Formik {...formik}>
        {(formikProps) => (
          <Form>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="title">WorkShop Title</label>
                <input
                  type="text"
                  className="input"
                  name="title"
                  placeholder="Type here"
                  value={formikProps.values.title}
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="title"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="email">Invite Instructor</label>
                <input
                  type="email"
                  className="input"
                  name="instructorEmail"
                  placeholder="Type nstructor email"
                  value={formikProps.values.instructorEmail}
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="instructorEmail"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="type">Workshop Type</label>
                <select
                  name="type"
                  className="input"
                  onChange={formikProps.handleChange}
                  value={formikProps.values.type}>
                  <option value="value-1">Virtual</option>
                  <option value="value-2">value-2</option>
                  <option value="value-3">value-3</option>
                  <option value="value-4">value-4</option>
                </select>
                <ErrorMessage
                  name="type"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="url">Link for WorkShop</label>
                <input
                  type="url"
                  className="input"
                  name="link"
                  placeholder="Paste URL here"
                  value={formikProps.values.link}
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="link"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <DragAndDropImage
              name="banner"
              value={getIn(formikProps.values, 'banner.link')}
              onChangeFile={(field, value) =>
                onChangeDragZone(field, value, formikProps.setFieldValue)
              }
            />
            <ErrorMessage
              name="banner.link"
              render={(error) => <span className="error">{error}</span>}
            />
            <div className="form-desc">
              <label htmlFor="description">Description</label>
              <textarea
                name="description"
                className="input"
                cols={0}
                rows={5}
                placeholder="Type here"
                value={formikProps.values.description}
                onChange={formikProps.handleChange}
              />
              <ErrorMessage
                name="description"
                render={(error) => <span className="error">{error}</span>}
              />
            </div>
            <div className="form-desc">
              <label htmlFor="description">Address</label>
              <input
                type="text"
                className="input"
                name="location"
                placeholder="Address"
                value={formikProps.values.location}
                onChange={formikProps.handleChange}
              />
              <ErrorMessage
                name="location"
                render={(error) => <span className="error">{error}</span>}
              />
            </div>
            {formikProps.values.data.map((item, index) => (
              <div className="form-by-day" key={index}>
                <div className="form-day">
                  <h1>Day {numberToWords(index + 1)}</h1>
                  {formikProps.values.data.length > 1 && (
                    <button
                      type="button"
                      onClick={() =>
                        onRemoveDay({
                          group: 'data',
                          values: formikProps.values.data,
                          onChange: formikProps.setFieldValue,
                          index: index,
                        })
                      }>
                      <span className="material-icons">delete_outline</span>Delete
                    </button>
                  )}
                </div>
                <div className="form-by-day-inputs">
                  <div className="input-field">
                    <label htmlFor="title">Title</label>
                    <input
                      type="text"
                      className="input"
                      name={`data.${index}.title`}
                      placeholder="Type here"
                      value={item.title}
                      onChange={formikProps.handleChange}
                    />
                    <ErrorMessage
                      name={`data.${index}.title`}
                      render={(error) => <span className="error">{error}</span>}
                    />
                  </div>
                  <div className="input-field">
                    <label htmlFor="date">Date</label>
                    <input
                      type="date"
                      className="input"
                      name={`data.${index}.date`}
                      value={moment(item.date).format(moment.HTML5_FMT.DATE)}
                      onChange={(event) =>
                        onChangeDate({
                          event: event,
                          value: item.date,
                          onChange: formikProps.setFieldValue,
                        })
                      }
                    />
                    <ErrorMessage
                      name={`data.${index}.date`}
                      render={(error) => <span className="error">{error}</span>}
                    />
                  </div>
                  <div className="input-field">
                    <label htmlFor="time">Time</label>
                    <input
                      type="time"
                      className="input"
                      name={`data.${index}.date`}
                      disabled={!item.date}
                      placeholder=""
                      value={moment(item.date).format(moment.HTML5_FMT.TIME)}
                      onChange={(event) =>
                        onChangeTime({
                          event: event,
                          value: item.date,
                          onChange: formikProps.setFieldValue,
                        })
                      }
                    />
                    <ErrorMessage
                      name={`data.${index}.time`}
                      render={(error) => <span className="error">{error}</span>}
                    />
                  </div>
                </div>
                <div className="form-desc">
                  <label htmlFor="description">Description</label>
                  <textarea
                    name={`data.${index}.description`}
                    className="input"
                    cols={0}
                    rows={5}
                    placeholder="Type here"
                    value={item.description}
                    onChange={formikProps.handleChange}
                  />
                  <ErrorMessage
                    name={`data.${index}.description`}
                    render={(error) => <span className="error">{error}</span>}
                  />
                </div>
              </div>
            ))}
            <div className="form-btns">
              <button disabled={!formikProps.isValid} type="submit">
                Save and Publish Workshop
              </button>
              <button
                type="button"
                onClick={() =>
                  onAddDay({
                    group: 'data',
                    values: formikProps.values.data,
                    onChange: formikProps.setFieldValue,
                  })
                }>
                Add Another Day
              </button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
}
