import { createSlice } from '@reduxjs/toolkit';
import { workshopDetailThunk } from './thunk';
import { WorkshopInterface } from '../../../interfaces/workshop.interface';

interface RegisterState {
  data: WorkshopInterface;
  loading: boolean;
  error: any;
  statusCode: number;
}

const initialState: RegisterState = {
  data: null,
  loading: false,
  error: null,
  statusCode: null,
};

export const workshopDetailSlice = createSlice({
  name: 'workshop-detail-slice',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    // Workshops List
    builder.addCase(workshopDetailThunk.pending, (state, { payload }) => {
      state.data = null;
      state.loading = true;
      state.error = null;
    });
    builder.addCase(workshopDetailThunk.fulfilled, (state, { payload }) => {
      state.data = payload;
      state.loading = false;
      state.error = null;
    });
    builder.addCase(workshopDetailThunk.rejected, (state, action) => {
      if (action.payload) {
        state.error = action.payload;
      } else {
        state.error = action.error;
      }
      state.loading = false;
    });
  },
});
