import React, { useEffect } from 'react';
import Educators from '../../components/Educators';
import { useAppDispatch, useAppSelector } from '../../../redux';
import { instructorListThunk } from '../Instructors/thunk';

export default function EducatorsView(): JSX.Element {
  const educators = useAppSelector((state) => state.educators);
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(instructorListThunk());
  }, [dispatch]);
  return (
    <div className="educators-section same-btns">
      <div className="user-section">
        <div className="user-work">
          <h1>All Educators</h1>
          <div className="user-work-btns">
            <button className="csv">
              <span className="material-icons">description</span>CSV
            </button>
            <div className="search-insturtor">
              <input type="text" placeholder="Search here" id="search" />
              <button>
                <label htmlFor="search">
                  <span className="material-icons">search</span>
                </label>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="content all-instructors">
        {educators.data.map((item) => (
          <Educators data={item} />
        ))}
      </div>
    </div>
  );
}
