import { createAsyncThunk } from '@reduxjs/toolkit';
import { API } from '../../utility/api';
import { WorkshopInterface } from '../../interfaces/workshop.interface';

export const workshopsListThunk = createAsyncThunk(
  `workshops-list-thunk`,
  async (data, thunkApi) => {
    try {
      const response = await API.Workshop.getAll();
      return response.data;
    } catch (error) {
      console.log(error.response);
      return thunkApi.rejectWithValue(error.response);
    }
  },
);

