import React, { ChangeEvent, createRef, useContext, useState } from 'react';
import InstructorProfile from '../../components/InstructorProfile';
import cardOne from '../../../assets/images/p-6.jpg';
import cardTwo from '../../../assets/images/p-5.jpg';
import cardThree from '../../../assets/images/p-1.jpg';
import profileImg from '../../../assets/images/Group 7.png';
import clsx from 'clsx';
import { AuthContext } from '../../context';
import { FormikConfig } from 'formik/dist/types';
import { UpdateUserValidator } from '../../../validators';
import { ErrorMessage, Form, Formik, FormikHelpers } from 'formik';
import { API } from '../../../utility/api';
import { toast } from 'react-toastify';
import { FormikValidator } from '../../../utility';

export default function InstructorProfileView(): JSX.Element {
  const authContext = useContext(AuthContext);
  const userFormSubmitRef = createRef<HTMLButtonElement>();
  const formik: FormikConfig<UpdateUserValidator> = {
    initialValues: {
      firstName: '',
      lastName: '',
      email: '',
      summerEmail: '',
      phoneNumber: '',
      address1: '',
      address2: '',
      zip: '',
      city: '',
      resume: '',
      profile: '',
      state: '',
      bio: '',
    },
    onSubmit(values: UpdateUserValidator, formikHelpers: FormikHelpers<any>): void | Promise<any> {
      API.Auth.updateUser(values)
        .then((response) => {
          authContext.actions.updateState(response.data);
          toast('User Updated Successfully', {
            type: 'success',
          });
        })
        .catch((error) => {
          console.error(error.response);
          const errors = FormikValidator.serverErrors(error.response);
          if (errors) {
            formikHelpers.setErrors(errors);
          }
        });
      return undefined;
    },
    validate: FormikValidator.validator(UpdateUserValidator),
  };

  const onChangeFile = (
    event: ChangeEvent,
    setFieldValue: (field: string, value: any, shouldValidate?: boolean) => void,
  ) => {
    const target = event.target as HTMLInputElement;
    const files = target.files;
    API.Media.uploadFile(files[0])
      .then((response) => {
        console.log(response);
        setFieldValue(target.name, response.data.path);
      })
      .catch((error) => {
        console.error(error);
      });
  };

  formik.initialValues = authContext.state.user;
  const cards = [
    {
      img: cardOne,
      title: 'Join us for special event featuring Arne Duncan, former U.S...',
      name: '- By Alberta Berg',
      date: 'Thursday, April 15',
      time: '10 am PST / 1 pm EST',
      location: '1710 Blanc Ln, FL 32577',
    },
    {
      img: cardTwo,
      title: 'Join us for special event featuring Arne Duncan, former U.S...',
      name: '- By Alberta Berg',
      date: 'Thursday, April 15',
      time: '10 am PST / 1 pm EST',
      location: '1710 Blanc Ln, FL 32577',
    },
    {
      img: cardThree,
      title: 'Join us for special event featuring Arne Duncan, former U.S...',
      name: '- By Alberta Berg',
      date: 'Thursday, April 15',
      time: '10 am PST / 1 pm EST',
      location: '1710 Blanc Ln, FL 32577',
    },
  ];
  const [workshopsType, setWorkshopsType] = useState('current');

  return (
    <div className="instructor-profile-section">
      <div className="user-section instructor-profile-btns">
        <div className="user-work">
          <h1>
            {authContext.state.user.firstName} {authContext.state.user.lastName}
          </h1>
          <button
            className="btn btn-primary"
            style={{
              background: '#0063B2',
              color: '#ffffff',
            }}
            onClick={() => {
              userFormSubmitRef.current.click();
            }}>
            Update Profile
          </button>
        </div>
      </div>
      <div className="content">
        <Formik {...formik}>
          {(formikProps) => (
            <Form>
              <div className="instructor-info">
                <div className="profile-pic-section">
                  <div className="profile-img d-flex flex-column w-100 align-items-center justify-content-center">
                    <div className="circular-image">
                      <img
                        alt=""
                        className="profile-image"
                        src={formikProps.values.profile || profileImg}
                      />
                      <label className="profile-edit">
                        <span className="material-icons">add_a_photo</span>
                        <input
                          type="file"
                          name="profile"
                          onChange={(event) => onChangeFile(event, formikProps.setFieldValue)}
                        />
                      </label>
                    </div>
                    <p>Jerry Dawson</p>
                  </div>
                  <div className="input-field">
                    <label htmlFor="address1">Address</label>
                    <input
                      type="text"
                      className="input"
                      name="address1"
                      placeholder="Address"
                      value={formikProps.values.address1}
                      onChange={formikProps.handleChange}
                    />
                    <ErrorMessage
                      name="address1"
                      render={(error) => <span className="error">{error}</span>}
                    />
                  </div>
                </div>
                <div className="data">
                  <h1>Personal Details</h1>
                  <div className="input-fields">
                    <div className="input-field">
                      <label htmlFor="firstName">First Name</label>
                      <input
                        type="text"
                        className="input"
                        name="firstName"
                        placeholder="First Name"
                        value={formikProps.values.firstName}
                        onChange={formikProps.handleChange}
                      />
                      <ErrorMessage
                        name="firstName"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                    <div className="input-field">
                      <label htmlFor="lastName">Last Name</label>
                      <input
                        type="text"
                        className="input"
                        name="lastName"
                        placeholder="Last Name"
                        value={formikProps.values.lastName}
                        onChange={formikProps.handleChange}
                      />
                      <ErrorMessage
                        name="lastName"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                  <div className="input-fields">
                    <div className="input-field">
                      <label htmlFor="email">Email Address</label>
                      <input
                        type="email"
                        className="input"
                        name="email"
                        placeholder="Enter your Email"
                        value={formikProps.values.email}
                        onChange={formikProps.handleChange}
                      />
                      <ErrorMessage
                        name="email"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                    <div className="input-field">
                      <label htmlFor="phoneNumber">Phone Number</label>
                      <input
                        type="text"
                        className="input"
                        name="phoneNumber"
                        placeholder="Enter your Phone Number"
                        value={formikProps.values.phoneNumber}
                        onChange={formikProps.handleChange}
                      />
                      <ErrorMessage
                        name="phoneNumber"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                  <div className="input-fields">
                    <div className="input-fields">
                      <div className="input-field">
                        <label htmlFor="city">City</label>
                        <input
                          type="text"
                          className="input"
                          name="city"
                          placeholder="City"
                          value={formikProps.values.city}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="city"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                      <div className="input-field">
                        <label htmlFor="state">State</label>
                        <input
                          type="text"
                          className="input"
                          name="state"
                          placeholder="State"
                          value={formikProps.values.state}
                          onChange={formikProps.handleChange}
                        />
                        <ErrorMessage
                          name="state"
                          render={(error) => <span className="error">{error}</span>}
                        />
                      </div>
                    </div>
                    <div className="input-field">
                      <label htmlFor="code">Zip Code</label>
                      <input
                        type="text"
                        className="input"
                        name="zip"
                        placeholder="Zip Code"
                        value={formikProps.values.zip}
                        onChange={formikProps.handleChange}
                      />
                      <ErrorMessage
                        name="zip"
                        render={(error) => <span className="error">{error}</span>}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="summary">
                <label htmlFor="bio">Personal Summary</label>
                <textarea
                  name="bio"
                  className="input"
                  cols={0}
                  rows={3}
                  value={formikProps.values.bio}
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="bio"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <button
                type="submit"
                ref={userFormSubmitRef}
                style={{
                  position: 'fixed',
                  left: -100,
                  top: -100,
                }}>
                Submit
              </button>
            </Form>
          )}
        </Formik>
      </div>
      <div className="">
        <div className="user-section">
          <div className="user-work">
            <div className="user-work-heaiding">
              <h1>WorkShops</h1>
            </div>
            <div className="user-work-btns">
              <button
                className={clsx({ active: workshopsType === 'current' })}
                onClick={() => setWorkshopsType('current')}>
                Current WorkShops
              </button>
              <button
                className={clsx({ active: workshopsType === 'past' })}
                onClick={() => setWorkshopsType('past')}>
                Past WorkShops
              </button>
            </div>
          </div>
        </div>
        {workshopsType === 'current' && (
          <>
            <div className="items content">
              {cards.map((item) => (
                <InstructorProfile data={item} />
              ))}
            </div>
          </>
        )}
        {workshopsType === 'past' && (
          <>
            <div className="items content">
              {cards.map((item) => (
                <InstructorProfile data={item} />
              ))}
            </div>
          </>
        )}
      </div>
      ;
    </div>
  );
}
