import { IsEmail, IsNotEmpty, IsNumber, IsPhoneNumber, IsString } from 'class-validator';
import { Transform } from 'class-transformer';
import { Match } from './custom-class-validator';

export class SignUpEducatorPersonValidator {
  @IsString()
  @IsNotEmpty()
  firstName: string;

  @IsString()
  @IsNotEmpty()
  lastName: string;

  @IsEmail()
  @IsNotEmpty()
  email: string;

  @IsEmail()
  @IsNotEmpty()
  summerEmail: string;

  @IsString()
  @IsNotEmpty()
  address1: string;

  @IsString()
  @IsNotEmpty()
  address2: string;

  @IsString()
  @IsNotEmpty()
  city: string;

  @IsString()
  @IsNotEmpty()
  state: string;

  @IsString()
  @IsNotEmpty()
  zip: string;

  @IsPhoneNumber()
  @IsNotEmpty()
  @Transform(
    ({ value }) => {
      console.log(value);
      return value.startsWith('+') ? value : '+' + value;
    },
    { toClassOnly: true },
  )
  phoneNumber: string;

  @IsString()
  @IsNotEmpty()
  password: string;

  @IsString()
  @IsNotEmpty()
  @Match('password', {
    message: 'Password and Confirm Password Must Be The Same',
  })
  confirm_password: string;
}

export class SignUpEducatorUpdateSchoolValidator {
  @IsString()
  @IsNotEmpty()
  district: string;

  @IsString()
  @IsNotEmpty()
  name: string;

  @IsString()
  @IsNotEmpty()
  setting: string;

  @IsString()
  @IsNotEmpty()
  type: string;

  @IsString()
  @IsNotEmpty()
  role: string;

  @IsNumber()
  @IsNotEmpty()
  students: number;

  @IsString()
  @IsNotEmpty()
  licenceAreas: string;

  @IsString()
  @IsNotEmpty()
  grads: string;

  @IsString()
  @IsNotEmpty()
  experience: string;

  @IsString()
  @IsNotEmpty()
  race: string;

  @IsString()
  @IsNotEmpty()
  condition: string;
}
