import React, { useContext } from 'react';
import { ErrorMessage, Form, Formik, FormikHelpers } from 'formik';
import { FormikConfig } from 'formik/dist/types';
import { SignUpEducatorUpdateSchoolValidator } from '../../../../../validators';
import { API } from '../../../../../utility/api';
import { UserInterface } from '../../../../../interfaces';
import { AuthContext } from '../../../../context';
import { useHistory } from 'react-router-dom';
import { FormikValidator } from '../../../../../utility';

type SignUpTabType = 'Personal' | 'School';

interface Props {
  onChangeTab: (tab: SignUpTabType) => void;
  user: UserInterface;
}

const SchoolDetails = (props: Props): JSX.Element => {
  const authContext = useContext(AuthContext);
  const history = useHistory();
  const formik: FormikConfig<SignUpEducatorUpdateSchoolValidator> = {
    initialValues: {
      district: '',
      name: '',
      setting: '',
      type: '',
      role: '',
      students: 0,
      licenceAreas: '',
      grads: '',
      experience: '',
      race: '',
      condition: '',
    },
    onSubmit(
      values: SignUpEducatorUpdateSchoolValidator,
      formikHelpers: FormikHelpers<any>,
    ): void | Promise<any> {
      console.log(values);
      API.Auth.signUpAsEducatorUpdateSchool(values)
        .then((response) => {
          console.log(response);
          props.onChangeTab('Personal');
          authContext.actions.updateState(null);
          formikHelpers.setSubmitting(false);
          history.push('/login');
        })
        .catch((error) => {
          const statusCode = error.response.status;
          console.log(error.response);
          const errors = FormikValidator.serverErrors(error.response);
          if (errors) {
            formikHelpers.setErrors(errors);
          }

          formikHelpers.setSubmitting(false);
        });
    },
    validate: FormikValidator.validator(SignUpEducatorUpdateSchoolValidator),
  };
  return (
    <div className="school-details">
      <Formik {...formik}>
        {(formikProps) => (
          <Form onSubmit={formikProps.handleSubmit}>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="schoolDistrict">School District</label>
                <input
                  type="text"
                  name="district"
                  className="input"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="district"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="lastName"> Name</label>
                <input
                  type="text"
                  name="name"
                  className="input"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="name"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-fields">
              <div className="input-fields">
                <div className="input-field">
                  <label htmlFor="type">School Setting</label>
                  <select name="setting" className="input" onChange={formikProps.handleChange}>
                    <option value="value-1">Select</option>
                    <option value="value-2">value-2</option>
                    <option value="value-3">value-3</option>
                    <option value="value-4">value-4</option>
                  </select>
                  <ErrorMessage
                    name="setting"
                    render={(error) => <span className="error">{error}</span>}
                  />
                </div>
                <div className="input-field">
                  <label htmlFor="type">School Type</label>
                  <select name="type" className="input" onChange={formikProps.handleChange}>
                    <option value="value-1">Select</option>
                    <option value="value-2">value-2</option>
                    <option value="value-3">value-3</option>
                    <option value="value-4">value-4</option>
                  </select>
                  <ErrorMessage
                    name="type"
                    render={(error) => <span className="error">{error}</span>}
                  />
                </div>
              </div>
              <div className="input-field">
                <label htmlFor="type">Primary Role</label>
                <select name="role" className="input" onChange={formikProps.handleChange}>
                  <option value="value-1">Select</option>
                  <option value="value-2">value-2</option>
                  <option value="value-3">value-3</option>
                  <option value="value-4">value-4</option>
                </select>
                <ErrorMessage
                  name="role"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-field">
              <label htmlFor="">On average, how many students do you typically work with?</label>
              <input
                type="number"
                name="students"
                className="input"
                placeholder="Type here"
                onChange={formikProps.handleChange}
              />
              <ErrorMessage
                name="students"
                render={(error) => <span className="error">{error}</span>}
              />
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="">Primary Licensure Areas</label>
                <input
                  type="text"
                  name="licenceAreas"
                  className="input"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="licenceAreas"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="">Grades You are Teaching</label>
                <input
                  type="text"
                  name="grads"
                  className="input"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="grads"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-fields">
              <div className="input-field">
                <label htmlFor="type">Prior Programming Experience</label>
                <select name="experience" className="input" onChange={formikProps.handleChange}>
                  <option value="value-1">Select</option>
                  <option value="value-2">value-2</option>
                  <option value="value-3">value-3</option>
                  <option value="value-4">value-4</option>
                </select>
                <ErrorMessage
                  name="experience"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
              <div className="input-field">
                <label htmlFor="">Race/ethnicity?</label>
                <input
                  type="text"
                  name="race"
                  className="input"
                  placeholder="Type here"
                  onChange={formikProps.handleChange}
                />
                <ErrorMessage
                  name="race"
                  render={(error) => <span className="error">{error}</span>}
                />
              </div>
            </div>
            <div className="input-field">
              <label htmlFor="type">
                Do you identify as a person with a disability or other chronic conditions?
              </label>
              <select name="condition" className="input" onChange={formikProps.handleChange}>
                <option value="value-1">Select</option>
                <option value="value-2">value-2</option>
                <option value="value-3">value-3</option>
                <option value="value-4">value-4</option>
              </select>
              <ErrorMessage
                name="condition"
                render={(error) => <span className="error">{error}</span>}
              />
            </div>
            <div className="input-field">
              <button type="submit">Sign Up</button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default SchoolDetails;
