import React, { MouseEvent } from 'react';
import notiIcon from '../../../assets/icons/Group 22.svg';
import separator from '../../../assets/icons/separator.svg';
import avater from '../../../assets/icons/Bitmap.svg';
import { Link } from 'react-router-dom';
import { Dropdown } from 'react-bootstrap';
import { useAuthContext } from '../../context';
import { UserRoleEnum } from '../../../interfaces';

const Header = (): JSX.Element => {
  const authContext = useAuthContext();

  const onClickLogout = (event: MouseEvent) => {
    event.preventDefault();
    authContext.actions.updateState(null);
  };
  return (
    <div>
      <div className="user-section">
        <header className="user-info">
          <div className="user-info-btns">
            {authContext.state.user.role === UserRoleEnum.admin && (
              <>
                <Link to="/createworkshop" className="new-workshop">
                  Create WorkShop
                </Link>
                <button>Create Script WorkShop</button>
              </>
            )}
          </div>
          <div className="user-profile">
            {authContext.state.user.role === UserRoleEnum.admin && (
              <>
                <div className="notification">
                  <div className="counter">3</div>
                  <img src={notiIcon} alt="" />
                </div>
                <img className="separator" src={separator} alt="" />
              </>
            )}

            <div className="profile">
              <div className="profile-pic">
                <img src={authContext.state.user.profile || avater} alt="" />
              </div>
              <div className="profile-name">
                <h1>John Grey</h1>
                <Dropdown className="header-dropdown">
                  <Dropdown.Toggle variant="success" id="dropdown-basic">
                    <p>Sign out</p>
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    {authContext.state.user.role === UserRoleEnum.instructor && (
                      <Link className="dropdown-item" to="/instructor-profile">
                        Update Profile
                      </Link>
                    )}

                    {authContext.state.user.role === UserRoleEnum.educator && (
                      <Link className="dropdown-item" to="/educator-profile">
                        Update Profile
                      </Link>
                    )}

                    <div className="dropdown-item" onClick={onClickLogout}>
                      Logout
                    </div>
                  </Dropdown.Menu>
                </Dropdown>
              </div>
            </div>
          </div>
        </header>
      </div>
    </div>
  );
};
export default Header;
