import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import Dashboard from '../views/Dashboard';
import Workshops from '../views/Workshops';
import Instructors from '../views/Instructors';
import Educators from '../views/Educators';
import InstructorProfile from '../views/InstructorProfile';
import CurrentWorkshop from '../views/CurrentWorkshop';
import CreateWorkshop from '../views/CreateWorkshop';
import WorkshopDetail from '../views/WorkshopDetail';
import Attendance from '../views/AttendanceList';
import ScriptWorkshopView from '../views/ScritpWorkshops';
import UpdateWorkshopView from '../views/UpdateWorkshop';
import EducatorProfileView from '../views/EducatorProfile';

export function Router(): JSX.Element {
  return (
    <Switch>
      <Route component={Dashboard} path="/dashboard" />
      <Route component={Workshops} path="/workshops" />
      <Route component={ScriptWorkshopView} path="/script-workshop-view" />
      <Route component={Instructors} path="/instructors" />
      <Route component={Educators} path="/educators" />
      <Route component={InstructorProfile} path="/instructor-profile" />
      <Route component={EducatorProfileView} path="/educator-profile" />
      <Route component={CurrentWorkshop} path="/current-workshop" />
      <Route component={CurrentWorkshop} path="/past-workshop" />
      <Route component={CreateWorkshop} path="/createworkshop" />
      <Route component={UpdateWorkshopView} path="/update-workshop/:id" />
      <Route component={WorkshopDetail} path="/workshop-detail/:id" />
      <Route component={Attendance} path="/attendance" />
      <Route component={Attendance} path="/workshop-attendance/:id" />
      <Redirect to="/dashboard" />
    </Switch>
  );
}
