import React from 'react';

interface Props {
  data: {
    img: string;
    name: string;
    id: string;
  };
}

const CurrentWorkshop = (props: Props): JSX.Element => {
  const { data } = props;
  return (
    <div className="attendance">
      <div className="attendance-avatar">
        <img src={data.img} alt="" />
      </div>
      <div className="attendance-desc">
        <h1>{data.name}</h1>
        <div className="radio-toolbar">
          <label>
            <input type="radio" name={data.id} value="present" />
            <span className="present">Present</span>
          </label>

          <label>
            <input type="radio" name={data.id} value="absent" />
            <span className="absent">Absent</span>
          </label>
        </div>
      </div>
    </div>
  );
};
export default CurrentWorkshop;
