import { createSlice } from '@reduxjs/toolkit';
import { instructorListThunk } from './thunk';
import { UserInterface } from '../../../interfaces';

interface RegisterState {
  data: UserInterface[];
  loading: boolean;
  error: any;
  statusCode: number;
}

const initialState: RegisterState = {
  data: [],
  loading: false,
  error: null,
  statusCode: null,
};

export const instructorsListSlice = createSlice({
  name: 'instructors-list-slice',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    // Workshops List
    builder.addCase(instructorListThunk.pending, (state, { payload }) => {
      state.data = [];
      state.loading = true;
      state.error = null;
    });
    builder.addCase(instructorListThunk.fulfilled, (state, { payload }) => {
      state.data = payload;
      state.loading = false;
      state.error = null;
    });
    builder.addCase(instructorListThunk.rejected, (state, action) => {
      if (action.payload) {
        state.error = action.payload;
      } else {
        state.error = action.error;
      }
      state.loading = false;
    });
  },
});
