import React, { useEffect, useState } from 'react';
import backBtn from '../../../assets/icons/Path.svg';
import DetailsMain from './components/DetailsMain';
import DetailsDay from './components/DetailsDay';
import DetailsInstructors from './components/DetailsInstructors';
import Libraries from './components/Libraries';
import DetailsEducators from './components/DetailsEducators';
import { Button, Modal } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../../redux';
import { RouteChildrenProps } from 'react-router';
import { workshopDetailThunk } from './thunk';
import { useAuthContext } from '../../context';
import { UserRoleEnum } from '../../../interfaces';

const WorkshopDetailView = (props: RouteChildrenProps<{ id: string }>): JSX.Element => {
  const authContext = useAuthContext();
  const [show, setShow] = useState(false);
  const workshop = useAppSelector((state) => state.workshopDetail);
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(workshopDetailThunk(props.match.params.id));
  }, [props.match.params, dispatch]);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const history = useHistory();
  const goback = () => {
    history.goBack();
  };
  return (
    <div className="details-section">
      <div className="user-section">
        <div className="user-work">
          <div className="user-work-back" onClick={goback}>
            <img alt="" src={backBtn} />
            <p>Back</p>
          </div>
          {authContext.state.user.role === UserRoleEnum.admin && (
            <div className="user-work-btns">
              <button onClick={handleShow}>
                <span className="material-icons">mail_outline</span>Email
              </button>
              <button>
                <span className="material-icons">edit</span>Edit
              </button>
            </div>
          )}

          {authContext.state.user.role === UserRoleEnum.instructor && (
            <div className="user-work-btns">
              <button onClick={() => history.push(`/workshop-attendance/${props.match.params.id}`)}>
                <span className="material-icons">manage_accounts</span>Attendance
              </button>
            </div>
          )}

          {authContext.state.user.role === UserRoleEnum.educator && (
            <div className="user-work-btns">
              <button onClick={handleShow}>
                <span className="material-icons">mail_outline</span>Email
              </button>
            </div>
          )}
        </div>
      </div>
      {!workshop.loading && workshop.data && (
        <>
          <div className="content details-container">
            <div className="details">
              <DetailsMain />
              <DetailsDay />
            </div>
            <div className="users-type">
              {authContext.state.user.role !== UserRoleEnum.instructor && <DetailsInstructors />}
              <Libraries />
              {authContext.state.user.role !== UserRoleEnum.educator && <DetailsEducators />}
            </div>
          </div>

          <Modal size="lg" show={show} onHide={handleClose} className="details-modal">
            <Modal.Header>
              <Modal.Title>How to Build a Mobile App Using Data Science</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className="input-feilds">
                <div className="input-field">
                  <label htmlFor="subject">Subject</label>
                  <input type="text" className="input" placeholder="Subject" />
                </div>
                <div className="input-field">
                  <label htmlFor="desc">Description</label>
                  <textarea name="desc" className="input" rows={3}></textarea>
                </div>
              </div>
            </Modal.Body>
            <Modal.Footer className="modal-footer">
              <Button onClick={handleClose}>Cancel</Button>
              <Button onClick={handleClose}>Send Email</Button>
            </Modal.Footer>
          </Modal>
        </>
      )}
    </div>
  );
};

export default WorkshopDetailView;
