import React from 'react';
import { AxiosPromise } from 'axios';
import { axiosInstance } from './axiosInstance';
import { UserInterface } from '../../interfaces';
import {
  LoginFormValidator,
  SignUpEducatorPersonValidator,
  SignUpEducatorUpdateSchoolValidator,
  SignUpInstructorValidator,
  UpdateUserValidator,
} from '../../validators';

export class Auth extends React.Component {
  static signUpInstructor(data: SignUpInstructorValidator): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/instructor/signup',
      method: 'post',
      data,
    });
  }

  static signUpAsEducatorPerson(data: SignUpEducatorPersonValidator): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/educator/signup',
      method: 'post',
      data,
    });
  }

  static signUpAsEducatorUpdateSchool(
    data: SignUpEducatorUpdateSchoolValidator,
  ): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/educator/UpdateSchool',
      method: 'post',
      data,
    });
  }

  static updateUser(data: UpdateUserValidator): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/update',
      method: 'post',
      data,
    });
  }

  static login(data: LoginFormValidator): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/login',
      method: 'post',
      data,
    });
  }

  static delete(id: string): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth',
      method: 'delete',
      params: {
        id,
      },
    });
  }

  static getById(id: string): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/byId',
      method: 'get',
      params: {
        id,
      },
    });
  }

  static me(): AxiosPromise<UserInterface> {
    return axiosInstance({
      url: '/auth/me',
      method: 'get',
    });
  }

  static count(): AxiosPromise<{ count: number }> {
    return axiosInstance({
      url: '/auth/count',
      method: 'get',
    });
  }

  static getInstructors(): AxiosPromise<UserInterface[]> {
    return axiosInstance({
      url: '/auth/instructors',
      method: 'get',
    });
  }

  static getEducators(): AxiosPromise<UserInterface[]> {
    return axiosInstance({
      url: '/auth/educators',
      method: 'get',
    });
  }
}
