import { IsEmail, IsNotEmpty, IsPhoneNumber, IsString, IsUrl } from 'class-validator';
import { Match } from './custom-class-validator';
import { Transform } from 'class-transformer';

export class SignUpInstructorValidator {
  @IsString()
  @IsNotEmpty()
  firstName: string;

  @IsString()
  @IsNotEmpty()
  lastName: string;

  @IsEmail()
  email: string;

  @IsPhoneNumber()
  @IsNotEmpty()
  @Transform(({ value }) => {
    return value.startsWith('+') ? value : '+' + value;
  })
  phoneNumber: string;

  @IsString()
  @IsNotEmpty({
    message: 'address should not be empty',
  })
  address1: string;

  @IsString()
  @IsNotEmpty()
  city: string;

  @IsString()
  @IsNotEmpty()
  state: string;

  @IsString()
  @IsNotEmpty()
  zip: string;

  @IsUrl()
  @IsNotEmpty()
  resume: string;

  @IsString()
  @IsNotEmpty()
  bio: string;

  @IsString()
  @IsNotEmpty()
  password: string;

  @IsString()
  @IsNotEmpty()
  @Match('password', {
    message: 'Password and Confirm Password Must Be The Same',
  })
  confirm_password: string;
}
