import React, { useEffect, useState } from 'react';
import clsx from 'clsx';
import { WorkshopCard } from '../../components';
import { useAppDispatch, useAppSelector, workshopsListThunk } from '../../../redux';

export default function WorkshopsView(): JSX.Element {
  const workshops = useAppSelector((state) => state.workshopsList);
  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(workshopsListThunk());
  }, [dispatch]);

  const [workshopsType, setWorkshopsType] = useState('current');
  return (
    <div>
      <div className="user-section">
        <div className="user-work">
          <div className="user-work-heading">
            <h1>WorkShops</h1>
          </div>
          <div className="user-work-btns">
            <button
              className={clsx({ active: workshopsType === 'current' })}
              onClick={() => setWorkshopsType('current')}>
              Current WorkShops
            </button>
            <button
              className={clsx({ active: workshopsType === 'past' })}
              onClick={() => setWorkshopsType('past')}>
              Past WorkShops
            </button>
          </div>
        </div>
      </div>

      {!workshops.loading && workshopsType === 'current' && (
        <>
          <div className="items content">
            {workshops.data.map((item, index) => (
              <div className="" key={index}>
                <WorkshopCard data={item} loading={false} />
              </div>
            ))}
          </div>
        </>
      )}
      {!workshops.loading && workshopsType === 'past' && (
        <>
          <div className="items content">
            {workshops.data.map((item, index) => (
              <div className="" key={index}>
                <WorkshopCard data={item} loading={false} />
              </div>
            ))}
          </div>
        </>
      )}

      {workshops.loading && (
        <div className="items content">
          {Array(10)
            .fill(1)
            .map((item, index) => (
              <div className="" key={index}>
                <WorkshopCard loading={true} />
              </div>
            ))}
        </div>
      )}
    </div>
  );
}
