import { AxiosPromise } from 'axios';
import { axiosInstance } from './axiosInstance';
import { WorkshopInterface } from '../../interfaces/workshop.interface';
import { CreateWorkshopFromValidator } from '../../validators';
import { WorkshopAddDaysValidator } from '../../validators/workshop/WorkshopAddDays.validator';

export class Workshop {
  static create(data: CreateWorkshopFromValidator): AxiosPromise<WorkshopInterface> {
    return axiosInstance({ url: '/workshop', method: 'post', data });
  }

  static patch(
    workshop: string,
    data: CreateWorkshopFromValidator,
  ): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop',
      method: 'patch',
      data,
      params: {
        workshop,
      },
    });
  }

  static getAll(): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop',
      method: 'get',
    });
  }

  static getById(id: string): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop/byId',
      method: 'get',
      params: {
        id,
      },
    });
  }

  static myWorkshops(id: string): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop/my',
      method: 'get',
      params: {
        id,
      },
    });
  }

  static deleteById(id: string): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop/byId',
      method: 'delete',
      params: {
        id,
      },
    });
  }

  static getByInstructor(id: string): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop/byInstructor',
      method: 'get',
      params: {
        id,
      },
    });
  }

  static getByEducator(id: string): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop/byEducator',
      method: 'get',
      params: {
        id,
      },
    });
  }

  static addDays(
    workshop: string,
    data: WorkshopAddDaysValidator[],
  ): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop/addDays',
      method: 'post',
      data: {
        data,
      },
      params: {
        workshop,
      },
    });
  }

  static updateDay(workshop: string, day: string, data: any): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop/updateDay',
      method: 'patch',
      data,
      params: {
        workshop,
        day,
      },
    });
  }

  static inviteInstructor(data: any): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop/inviteInstructor',
      method: 'post',
      data,
    });
  }

  static inviteEducators(data: any): AxiosPromise<WorkshopInterface> {
    return axiosInstance({
      url: '/workshop/inviteEducators',
      method: 'post',
      data,
    });
  }

  static past(): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop/past',
      method: 'get',
    });
  }

  static current(): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop/current',
      method: 'get',
    });
  }

  static future(): AxiosPromise<WorkshopInterface[]> {
    return axiosInstance({
      url: '/workshop/future',
      method: 'get',
    });
  }

  static futureCount(): AxiosPromise<{
    count: number;
  }> {
    return axiosInstance({
      url: '/workshop/count/future',
      method: 'get',
    });
  }

  static pastCount(): AxiosPromise<{
    count: number;
  }> {
    return axiosInstance({
      url: '/workshop/count/past',
      method: 'get',
    });
  }

  static currentCount(): AxiosPromise<{
    count: number;
  }> {
    return axiosInstance({
      url: '/workshop/count/current',
      method: 'get',
    });
  }
}
