import { Action, configureStore, ThunkAction } from '@reduxjs/toolkit';
import { registerUserSlice } from '../app/views/SignUpAsInstructor/slice';
import { workshopsListSlice } from './workshops';
import { workshopDetailSlice } from '../app/views/WorkshopDetail';
import { instructorsListSlice } from '../app/views/Instructors/slice';
import { educatorsListSlice } from '../app/views/Educators/slice';

export const store = configureStore({
  reducer: {
    registerUser: registerUserSlice.reducer,
    workshopsList: workshopsListSlice.reducer,
    workshopDetail: workshopDetailSlice.reducer,
    instructors: instructorsListSlice.reducer,
    educators: educatorsListSlice.reducer,
  },
});

export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>;
