import React from 'react';
import avatar from '../../../../../assets/icons/user-1.svg';
import email from '../../../../../assets/icons/Fill 121.svg';
import dropdownIcon from '../../../../../assets/icons/Group 89 Copy 2.svg';
import { Dropdown } from 'react-bootstrap';
import { useAppSelector } from '../../../../../redux';

const DetailsEducators = (): JSX.Element => {
  const workshop = useAppSelector((state) => state.workshopDetail);
  return (
    <>
      {!!workshop.data?.attendees?.length && (
        <div className="educators">
          <h1>Educators</h1>
          {workshop.data.attendees.map((attendee) => (
            <div className="educator any-user">
              <div className="any-user-flex">
                <div>
                  <img src={attendee.userId.profile || avatar} alt="" />
                </div>
                <div>
                  <p>
                    {attendee.userId.firstName} {attendee.userId.lastName}
                  </p>
                  <div className="user-email">
                    <img src={email} alt="" />
                    <p>{attendee.email}</p>
                  </div>
                </div>
              </div>
              <Dropdown className="this-dropdown">
                <Dropdown.Toggle variant="success" id="dropdown-basic">
                  <div>
                    <img src={dropdownIcon} alt="" />
                  </div>
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
                  <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
                  <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            </div>
          ))}
        </div>
      )}
    </>
  );
};

export default DetailsEducators;
